@import url(https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;1,300;1,400;1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.audio-player{margin:60px}.landing-page{overflow:hidden;display:flex;justify-content:center;flex-direction:column;align-items:center;max-height:1200px;transition:max-height 6s ease-in-out}.landing-page-below-fold{display:flex;justify-content:center;flex-direction:column;align-items:center}.hidden{max-height:0}

a,a:hover,a:visited{text-decoration:inherit;color:inherit}body{background-color:#fafafa;font-family:'Lato', sans-serif}nav{border-bottom:1px solid black;background-color:white;display:flex}@media (max-width: 1000px){nav{display:none}}@keyframes logo-animation{0%{width:100px;margin-left:10px}10%{width:120px;margin-left:0}20%{width:100px;margin-left:10px}}.display-none{display:none}

